@import "_partial/_config";

/* background */
.mediaPress,
.mediaPublished,
.mediaLibrary,
.mediaNewsList,
.mediaNewsDetail {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}

/* -----------------------------------------------------------------------
## press #################################################################
----------------------------------------------------------------------- */
.press {
	padding-top: 16px;
	&_contact {
		.col2Box {
			margin-top: 16px;
			@include fontsize(14);
		}
		&_tels {
			margin-top: 1em;
		}
	}
	&_release {
		.col2Box {
			flex-wrap: wrap;
			margin-top: 16px;
			&_item {
				flex: 40%;
				border-top: 1px solid $color_line;
				padding-top: 16px;
				margin-top: 32px;
			}
			.btn.pdf {
				margin-top: 16px;
				a {
					border: 1px solid $color_line;
				}
			}
		}
	}
}
@include mediaQuery(sp){
	.press {
		&_contact {
			&_address {
				margin-top: 1em;
			}
		}
	}
}


/* -----------------------------------------------------------------------
## library ###############################################################
----------------------------------------------------------------------- */
.library {
	padding-top: 16px;
	&_overview {
		margin-top: 16px;
	}
	.col2Box {
		margin-top: 32px;
		&_item {
			.youtube {
				position: relative;
				padding-top: 51.26%;
			}
			iframe {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}
	.photoBox {
		margin-top: 32px;
		margin-left: -16px;
		&_item {
			float: left;
			margin-top: 16px;
			margin-left: 16px;
		}
	}
	.btn {
		margin-top: 32px;
		text-align: center;
		a {
			border: 1px solid $color_line;
		}
	}
}
@include mediaQuery(sp){
	.library {
		.col2Box {
			&_item {
				margin-top: 16px;
				.youtube {
					position: relative;
					padding-top: 51.26%;
				}
			}
		}
		.photoBox {
			margin-left: 0;
			&_item {
				width: 47.5%;
				margin-left: 0;
			}
			&_item:nth-child(even) {
				float: right;
				width: 47.5%;
			}
			img {
				width: 100%;
			}
		}
	}
}


/* -----------------------------------------------------------------------
## published #############################################################
----------------------------------------------------------------------- */
.published {
	padding-top: 16px;
	.selectBox {
		margin-top: 16px;
		text-align: center;
		select {
			line-height: 1;
			padding: 14px 32px;
			border-radius: 1000px;
			text-align: center;
			color: $color_text_dark;
			background: $color_bg_light;
		}
	}
	.publishedList {
		margin-left: -32px;
		&_item {
			float: left;
			width: 29.7%;
			margin: 64px 0 0 32px;
			border-top: 2px solid $color_line;
			text-align: center;
		}
		time {
			display: block;
			margin-top: 8px;
			font-weight: bold;
		}
		&_category {
			display: inline-block;
			margin-top: 8px;
			padding: 4px 32px;
			border-radius: 1000px;
			background: $color_bg_light;
			color: $color_text_dark;
		}
		&_img {
			margin-top: 8px;
		}
		&_text {
			margin-top: 8px;
			text-align: left;
			@include fontsize(14);
		}
	}
	.pager {
		display: flex;
		margin-top: 64px;
		.pagenation {
			flex: 1;
			text-align: center;
			&_item {
				display: inline-block;
				a {
					display: block;
					line-height: inherit;
					padding: 8px 14px;
					border: 2px solid $color_line;
					border-radius: 0;
				}
				&.active span {
					display: block;
					padding: 8px 14px;
					border: 2px solid $color_line;
					background: $color_bg_light;
					color: $color_text_dark;
					font-weight: bold;
				}
			}
		}
	}
}
@include mediaQuery(sp) {
	.published {
		.publishedList {
			margin-left: 0;
			&_item {
				width: 48%;
				margin-left: 0;
				img {
					width: 100%;
				}
			}
			&_item:nth-child(even) {
				float: right;
			}
			&_text {
				@include fontsize(14);
			}
		}
		.pager {
			display: block;
			&_prev,
			&_next {
				text-align: center;
				a {
					display: block;
					border: 1px solid $color_line;
				}
			}
			.pagenation {
				display: block;
				padding: 8px 0;
			}
		}
	}
}


/* -----------------------------------------------------------------------
## news list #############################################################
----------------------------------------------------------------------- */
.newsList {
	.mainSubBox {
		margin-top: 32px;
		&_main {
			padding-right: 16px;
		}
		&_sub {
			margin-left: 16px;
			padding: 8px;
			border-top: 1px solid $color_line;
		}
	}
	.categoryList,
	.archiveList {
		padding-bottom: 16px;
		border-bottom: 1px solid $color_line;
		@include fontsize(14);
		&_item {
			margin-top: 8px;
		}
	}
	.categoryList {
		padding-bottom: 32px;
		margin-bottom: 8px;
	}
	.mainSubBox_block {
		margin-top: 32px;
		border-top: 1px solid $color_line;
		&:first-child {
			margin-top: 0;
		}
	}
	.block_title {
		margin-top: 16px;
		@include fontsize(20);
	}
	.block_date {
		margin-top: 8px;
		@include fontsize(14);
	}
	.block_category {
		display: inline-block;
		line-height: 1;
		margin-top: 8px;
		padding: 8px 16px;
		border-radius: 1000px;
		background: $color_bg_light;
		color: $color_text_dark;
		@include fontsize(12);
	}
	.pager {
		display: flex;
		margin-top: 64px;
		.pagenation {
			flex: 1;
			text-align: center;
			&_item {
				display: inline-block;
				a {
					display: block;
					line-height: inherit;
					padding: 8px 14px;
					border: 2px solid $color_line;
					border-radius: 0;
				}
				&.active span {
					display: block;
					padding: 8px 14px;
					border: 2px solid $color_line;
					background: $color_bg_light;
					color: $color_text_dark;
					font-weight: bold;
				}
			}
		}
	}
}


@include mediaQuery(sp){
	.newsList {
		.mainSubBox {
			margin-top: 16px;
			&_main {
				padding-right: 0;
			}
			&_sub {
				margin-top: 16px;
				margin-left: 0;
				padding: 0;
			}
		}
		.mainSubBox_block {
			margin-top: 32px;
		}
		.asideTitle {
			padding-top: 4px;
		}
		.pager {
			display: block;
			&_prev,
			&_next {
				text-align: center;
				a {
					display: block;
					border: 1px solid $color_line;
				}
			}
			.pagenation {
				display: block;
				padding: 8px 0;
			}
		}
	}
}


